@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{ 
  margin: 0;
  background-image:  url(/App_Weather/static/media/weather.90e24d62.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.App{width:700px;height:100px;margin-top:30vh;margin-left:auto;margin-right:auto;font-family:"Roboto",sans-serif}
input{display:block;margin:0 auto;padding:5px;width:80%;height:50px;font-size:40px;text-transform:uppercase;font-weight:bold;background-color:rgba(49,125,211,.4);border-radius:3em}
.result{width:100%;margin-top:10px;border-radius:3em;font-size:30px;text-align:center}.result div{opacity:1;text-align:left;padding:10px;border-radius:2em;background-color:rgba(255,255,255,.4)}.result h1{font-size:35px}.result h2{font-size:30px}
